* {
  box-sizing: border-box;
}
.app {
  .programming {
    color: inherit;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 20px;
    font-size: 17px;
    gap: 5px;
  }
  .image.faresPre,
  .image.faresSec,
  .image.faresMed {
    // position: relative;
    // width: 760px;
    img {
      width: 100%;
    }
    .info {
      position: absolute;
      top: 0;
      padding-top: 135px;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      height: 100%;
      img {
        object-fit: cover;
        width: 200px;
        height: 200px;
        position: absolute;
        top: 339px;
        left: 93px;
        border-radius: 50%;
      }
      & > span,
      .bottom {
        // font-family: "CAIRO";
        // font-family: "MyFont";
        font-family: "GEP";
        width: -moz-fit-content;
        width: fit-content;
        color: #ee7023;
        font-weight: 900;
        text-shadow: 0.5px 0.5px 0px #c1b5b5;
        position: absolute;
      }
      .name {
        top: 603px;
        right: 111px;
        font-size: 32px;
        display: flex;
        align-items: center;
      }
      .year {
        top: 863px;
        left: 163px;
        font-size: 30px;
        display: flex;
        align-items: center;
      }
      .term {
        top: 863px;
        right: 182px;
        font-size: 30px;
      }
      .grad {
        top: 671px;

        right: 111px;
        font-size: 32px;
        display: flex;
        align-items: center;
      }

      .bottom {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 18px;
        top: 965px;
        min-width: 251px;
        left: 90px;
        color: #077292;
        font-size: 25px;
        span {
          font-family: "GEM";

          width: fit-content;
          color: #ee7023;
          font-weight: 700;
          text-shadow: 0.5px 0.5px 0px #c1b5b5;
        }
        .manager {
          color: inherit;
        }
        .managerName {
          color: inherit;
        }
      }
    }
  }
}
