.image.imgEeid {
  // position: relative;
  // width: 760px;
  img {
    width: 100%;
  }
  .info {
    position: absolute;
    top: 0;
    padding-top: 135px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    img.imgEid {
      width: 347px;
      height: 403px;
      position: absolute;
      top: 409px;
      left: 26px;
      border-radius: 0;
      z-index: -1;
    }
    & > span,
    .bottom {
      // font-family: "CAIRO";
      // font-family: "MyFont";
      font-family: "GEP";
      width: -moz-fit-content;
      width: fit-content;
      // color: #ec9023;
      color: #077292;
      font-weight: 900;
      text-shadow: 0.5px 0.5px 0px #6c6969;
      position: absolute;
    }
    .name {
      top: 736px;
      right: 42px;
      font-size: 34px;
      text-align: center;
      width: 419px;
      color: #010000;
      // color: #ec9023;
  
      // color: #077292;
    }
    .year {
      top: 863px;
      left: 163px;
      font-size: 31px;
    }
    .term {
      top: 863px;
      right: 182px;
      font-size: 31px;
    }
    .grad {
      color: #ec9023;
      top: 556px;
      right: 120px;
      font-size: 33px;
    }
    .family {
      right: 91px;
      top: 715px;
      font-size: 23px;
    }
    .share {
      right: 78px;
      top: 645px;
      font-size: 26px;
      width: 678px;
      text-align: center;
    }

    .bottom {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 31px;
      top: 949px;
      min-width: 251px;
      left: 121px;
      color: #077292;
      
      font-size: 24px;
      span {
        font-family: "GEM";

        width: fit-content;
        color: #ee7023;
        font-weight: 700;
        text-shadow: 0.5px 0.5px 0px #c1b5b5;
      }
      .manager {
        color: inherit;
      }
      .managerName {
        color: inherit;
      }
    }
  }
}
