@font-face {
  font-family: "ramadan";
  src: URL("../../fonts/ramdan-Abeer_2711.ttf") format("truetype");
}
@font-face {
  font-family: "Mohammad";
  src: URL("../../fonts/Mohammad-Bold-normal.ttf") format("truetype");
}

.image.coverReport {
  // position: relative;
  // width: 760px;
  img {
    width: 100%;
  }
  .info {
    position: absolute;
    top: 0;
    padding-top: 135px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;

    & > span,
    .bottom,
    .programDesc {
      // font-family: "CAIRO";
      // font-family: "MyFont";
      font-family: "GEP";
      width: fit-content;
      // color: #ec9023;
      color: #077292;
      font-weight: 900;
      text-shadow: 0.5px 0.5px 0px #6c6969;
      position: absolute;
    }
    p {
      font-family: "GEP";
    }
    .programName {
      top: 298px;
      right: 169px;
      font-size: 29px;
      color: #ff7800;
      width: 456px;
      text-align: center;
    }
    .programAmount,
    .programManager,
    .tripPeople,
    .programPlace,
    .programTime {
      right: 188px;
      font-size: 18px;
    }
    .programManager {
      top: 362px;
      color: #4bb0f8;
    }
    .programPlace {
      top: 410px;
      color: #f8c34b;
      font-size: 17px !important;
    }
    .tripPeople {
      top: 460px;
      color: #c7f84b;
    }
    .s-font {
      font-size: 18px;
      font-family: "GEM";
    }

    // reduce font size
    .s-font {
      display: inline-block;
      max-width: 179px; /* set the maximum width of the span */
      white-space: nowrap; /* prevent the text from wrapping */
      overflow: hidden; /* hide the overflow */
      text-overflow: ellipsis; /* add an ellipsis at the end of the truncated text */
    }

    .s-font.shrink {
      font-size: 84% !important; /* reduce the font size when the text overflows */
    }
    // reduce font size

    .programAmount {
      top: 506px;
      color: #f86b4b;
    }
    .programTime {
      top: 551px;
      color: #f8934b;
    }
    .programDesc {
      top: 402px;
      width: 353px;
      right: 394px;
      font-size: 19px;
      // word-spacing: 1.5px;
      line-height: 1.7;
      color: black;
      font-family: "Mohammad";
      // font-family: "GEM";
      font-weight: 500;
      padding: 5px 5px 0 0;
      height: 182px;
    }
    .imgR {
      object-fit: cover;
      position: absolute;
      height: 171px;
      width: 299px;
      z-index: 1;
    }
    .img1 {
      top: 630px;
      right: 65px;
    }
    .img2 {
      top: 630px;
      right: 425px;
    }
    .img3 {
      right: 65px;
      top: 830px;
    }
    .img4 {
      top: 830px;
      right: 425px;
    }

    .managerName {
      text-align: center;
      color: #02070a;
      font-size: 12px;
      bottom: 36px;
      width: 262px;
      right: 538px;
      font-weight: 200;
    }
    .activityLeader {
      bottom: 36px;
      right: -14px;
      width: 249px;
      text-align: center;
      color: #02070a;
      font-size: 12px;
      font-weight: 200;
    }

    .activityLeaderConstant {
      bottom: 60px;
      right: -15px;
      width: 249px;
      text-align: center;
      color: #02070a;
      font-size: 12px;
      font-weight: 200;
    }
    .managerNameConstant {
      bottom: 60px;
      right: 545px;
      width: 249px;
      text-align: center;
      color: #02070a;
      font-size: 12px;
      font-weight: 200;
    }
  }
}
