/* تنسيق footer */
.footer {
  background-color: #1f2937; /* لون الخلفية الرمادي */
  color: white;
  padding: 16px 0;
  margin-top: 20px;
  position: relative;
  z-index: 22222;
}

.footer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;
}

.footer-text {
  font-size: 14px;
  text-align: center;
}

.footer-bold {
  color: #10b981; /* اللون الأخضر */
  font-weight: bold;
}

/* زر واتساب */
.whatsapp-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: #10b981; /* اللون الأخضر */
  color: white;
  padding: 8px 16px;
  border-radius: 8px;
  transition: background-color 0.3s;
  text-decoration: none;
}

.whatsapp-btn:hover {
  background-color: #16a34a; /* تغيير اللون عند المرور */
}

.whatsapp-text {
  font-size: 14px;
  font-weight: 600;
}
