@font-face {
  font-family: "MyFont";
  src: URL("../../fonts/A\ GOOGLE\ Regular.ttf") format("truetype");
}
@font-face {
  font-family: "GEP";
  src: URL("../../fonts/ArbFONTS-GE_SS_Two_Bold_4.otf") format("truetype");
}
@font-face {
  font-family: "GEM";
  src: URL("../../fonts/ArbFONTS-GE_SS_TWO_MEDIUM_5.otf") format("truetype");
}
body {
  direction: rtl;
      unicode-bidi: embed;
       word-spacing: normal; /* ضبط المسافة بين الكلمات */
  white-space: normal; /* التأكد من الحفاظ على الفراغات */
}
* {
  font-kerning: normal;
  text-rendering: optimizeLegibility;
}
div, span {
      word-spacing: 0.01em;
}

.home {
  color: black;
  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
    max-width: 100vw;
    .imageContainer {
      max-width: 100vw;
      overflow: auto;
    }

    .carry {
      font-family: "GEP" !important;

      position: relative;
      aspect-ratio: 1/1.414;
      width: 210mm;
      height: 297mm;
      .image {
        display: flex;
      }
      &.carryEid {
        aspect-ratio: 1/1.02259 !important;
        width: 210mm;
        height: 214.74mm;
        .image {
          aspect-ratio: 1/1.02259 !important;
          width: 210mm;
          height: 214.74mm;
        }
      }
      &.carryHoliday {
        aspect-ratio: 1/1 !important;
        width: 210mm;
        height: 210mm;
        .image {
          aspect-ratio: 1/1 !important;
          width: 210mm;
          height: 210mm;
        }
      }
    }
    .image {
      position: relative;
      aspect-ratio: 1/1.414;
      width: 210mm;
      display: flex;
      height: 297mm;
    }
    .image.CongratulationsGrad3 {
      // position: relative;
      // width: 760px;
      img {
        width: 100%;
      }
      .info {
        position: absolute;
        top: 0;
        padding-top: 135px;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        height: 100%;
        img {
          object-fit: cover;
          width: 245px;
          height: 294px;
          position: absolute;
          top: 296px;
          left: 61px;
          border-radius: 104px;
          z-index: -1;
        }
        & > span,
        .bottom {
          // font-family: "CAIRO";
          // font-family: "MyFont";
          font-family: "GEP";

          width: fit-content;
          color: #ec9023;
          font-weight: 900;
          text-shadow: 0.5px 0.5px 0px black;

          position: absolute;
        }
        .name {
          top: 625px;
          right: 65px;
          font-size: 31px;
        }
        .grad {
          top: 695px;
          right: 65px;
          font-size: 31px;
        }
        .year {
          top: 346px;
          right: 232px;
          font-size: 27px;
          text-shadow: 0.5px 0.4px 0px black;
          color: #e78d23;
        }

        .bottom {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 17px;
          top: 966px;
          min-width: 300px;
          left: 96px;
          font-size: 25px;
          span {
            font-family: "GEM";

            width: fit-content;
            color: #077292;
            font-weight: 700;
            text-shadow: 0.5px 0.5px 0px black;
          }
          .manager {
            color: inherit;
          }
          .managerName {
            color: inherit;
          }
        }
      }
    }
    form {
      display: flex;
      width: 100%;
      align-items: center;
      flex-direction: column;
      padding: 40px 20px;
      gap: 20px;
      .logo {
        padding: 15px;
        box-shadow: 1px 1px 8px 0px #e78d23;
        display: flex;
        align-items: center;
        flex-direction: column;
        gap: 10px;
        h1:first-of-type {
          font-size: 22px;
          color: #ef4923;
        }
        h1:last-of-type {
          font-size: 19px;
          margin: 0;
          color: #5fccf2;
        }
        img {
          color: inherit;
        }
      }
      h1 {
        font-size: 20px;
        font-weight: 700;
        line-height: 30px;
        text-align: center;
        font-family: "cairo", sans-serif;
        margin: 10px 0;
      }
      //   h1:last-of-type {
      //     color: black;
      //   }
      input,
      textarea,
      select {
        border-radius: 6px !important;
        font-size: 19px !important;
        border: 1px solid #f9b872;
        text-align: center;
        width: 400px;
        max-width: 90% !important;
        height: 40px;
        vertical-align: middle;
        display: inline-block;
        max-width: 100%;
        padding: 0 10px;
        background: #fff;
        color: #666;
        transition: 0.2s ease-in-out;
        transition-property: color, background-color, border;
        font-weight: 600;
        &::placeholder {
          color: #d1d0d0;
        }
      }
      textarea {
        height: 140px !important;
        padding-top: 10px;
      }
    }
  }
  .reportImgLabel,
  label {
    font-family: "GEP";
    display: flex;
    align-items: center;
    gap: 5px;
  }
  button {
    border-radius: 6px !important;
    font-size: 17px !important;
    padding: 3px !important;
    width: 200px !important;
    font-family: "Amiri", serif !important;
    background-color: #f9b872;
    border: none;
    color: #fff;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    transition-duration: 0.4s;
    overflow: visible;
    font: inherit;
    box-sizing: border-box;
    vertical-align: middle;
    line-height: 38px;
    text-transform: uppercase;
    transition: 0.1s ease-in-out;
    transition-property: color, background-color, border-color;
    cursor: pointer;
    position: fixed;
    bottom: 30px;
    cursor: pointer;
    svg {
      margin-bottom: -9px;
    }
    &:hover {
      background-color: #fcc604;
    }
  }
  .qrImg {
    position: absolute;
    top: 777px;
    right: 358px;
    z-index: 22;
  }
  .progress {
    margin-top: 10px;
    font-size: 16px;
    color: green;
  }
}


// generate from table
/* تنسيق الحاوية */
.containeraaa {
  padding: 20px;
  text-align: center;
  z-index: 200000;
}

/* زر فتح النافذة */
.open-popup-btn {
background-color: #ee7023;
  color: white;
  border: none;
  padding: 10px 18px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 5px;
  font-weight: bolder;
}

.open-popup-btn:hover {
  background-color: #ee9523;
}

/* تنسيق النافذة المنبثقة */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

/* تعديل حجم النافذة لتكون أصغر */
.popup-content {
  background: white;
  padding: 15px;
  width: 85%;
  // max-width: 550px; /* أقصى عرض للنافذة */
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  text-align: center;
  overflow-y: auto; /* شريط تمرير إذا كان المحتوى طويل */
    height: 90vh; /* أقصى ارتفاع */
    max-height: 95vh; /* أقصى ارتفاع */
    display: flex;
  flex-direction: column;
}

/* تعديل حجم الجدول ليكون أصغر */
.student-table {
  width: 100%;
  border-collapse: collapse;
  margin: 10px 0;
  // max-height: 300px; /* تقليل حجم الجدول */
  overflow-y: auto;
  display: block;
  flex: 1;
}

/* إضافة شريط تمرير عند الحاجة */
.student-table tbody {
  display: block;
  // max-height: 200px; /* الارتفاع الأقصى للصفوف */
  overflow-y: auto;
}

.student-table thead,
.student-table tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}

/* تنسيق خلايا الجدول */
.student-table th, .student-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
  font-size: 14px; /* تصغير حجم الخط */
}

/* تلوين رأس الجدول */
.student-table th {
  background-color: #f4f4f4;
}

/* تنسيق حقل الإدخال */
.input-field {
  width: 100%;
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 13px;
}

/* زر الحذف */
.delete-btn {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 6px 10px;
  font-size: 12px;
  cursor: pointer;
  border-radius: 4px;
  text-align: center;
}

.delete-btn:hover {
  background-color: #c82333;
}

/* أزرار التحكم */
.div-group {
  margin-top: 10px;
}

.add-btn, .save-btn, .close-popup-btn {
padding: 8px 12px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  margin: 5px;
  font-weight: bolder;


}

.add-btn {
  background-color: #28a745;
  color: white;
}

.add-btn:hover {
  background-color: #218838;
}

.save-btn {
  background-color: #fcc604;
  color: black;
}

.save-btn:hover {
  background-color: #fcb204;
}

/* زر إغلاق النافذة */
.close-popup-btn {
  background-color: #6c757d;
  // background-color: #ff9900;
  color: white;
}

.close-popup-btn:hover {
  background-color: #5a6268;
}

/* توسيط زر الحذف */
.center {
  text-align: center;
    width: 20%;
}
.deleteRowT {
  width: 20%;
}


.disabled-btn {
  background-color: #ccc !important;
  color: #666 !important;
  cursor: not-allowed !important;
}

