.merge-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.file-input {
  padding: 10px;
  border: 2px solid #007BFF;
  border-radius: 10px;
  cursor: pointer;
  background-color: #f8f9fa;
  width: 300px;
  text-align: center;
}

.merge-button {
  background-color: #ee7023;
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: background 0.3s;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);

  &:hover {
    background-color: #ee9523;
  }
}
